import styles from "../css/Dots.module.scss";
const Dots = ({ page }) => {
  return (
    <div className={styles.dots}>
      <div className={page.home ? styles.brightdot : styles.dot}></div>
      <div className={page.projects ? styles.brightdot : styles.dot}></div>
      <div className={page.about ? styles.brightdot : styles.dot}></div>
      <div className={page.resume ? styles.brightdot : styles.dot}></div>
    </div>
  );
};

export default Dots;
